html {
  box-sizing: border-box;
}

/* * {
  outline: 1px solid #ee3131;
} */

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  display: block;
  max-width: 100%;
}

p,
ul,
li {
  margin: 0;
  padding: 0;
}
.root {
  padding-left: 200px;
  padding-right: 200px;
  background-color: blue;
}

